<template>
  <div class="order">
    <van-nav-bar title="我的订单" left-arrow @click-left="$router.go(-1)" />
    <van-tabs v-model="active">
      <van-tab name="all" title="全部"></van-tab>
      <van-tab name="payment" title="待支付"></van-tab>
      <van-tab name="delivery" title="待发货"></van-tab>
      <van-tab name="received" title="待收货"></van-tab>
      <van-tab name="comment" title="待评价"></van-tab>
    </van-tabs>
    <OrderListItem v-for="item in list" :key="item.order_id" :item="item" :totonum="totonum" :totalPrice="totalPrice"></OrderListItem>
  </div>
</template>

<script>
import {getuserorer} from '@/api/order.js'
import OrderListItem from '@/components/OrderListItem.vue'
export default {
  name: 'OrderPage',
  components: {
    OrderListItem
  },
  data () {
    return {
      active: this.$route.query.dataType||'all',
      page:1,
      list:[]
    }
  },

  watch:{
    active:{
      immediate:true,
      handler(){
        this.getuserorder()
      }
    }
  },

  methods:{
    async  getuserorder(){
    let a=  await getuserorer(this.active,this.page)
    this.list=a.data.list.data
    console.log(this.list);
    }
  },

  computed:{
     totonum(){
      let  num=0
      this.list.forEach(item=>{
         item.goods.forEach(item2=>{
            num+=item2.total_num
         })
      })

      return num
     },

     totalPrice(){
      let  num=0
      this.list.forEach(item=>{
         item.goods.forEach(item2=>{
            num+=item2.total_num*item2.goods_price
         })
      })
      num=num.toFixed(2)
      return  num
     }
  },

 created(){
    this.getuserorder()
  }
}
</script>

<style lang="less" scoped>
.order {
  background-color: #fafafa;
}
.van-tabs {
  position: sticky;
  top: 0;
}
</style>